.DSidebar{
  width: 20%;
  float: left;
  margin-top: 30px;
  padding-right: 20px;
  .menu-item{
    display: grid;
    grid-template-columns: 20% auto;
    align-items: center;
    grid-gap: 10px;
    text-align: left;
    margin-bottom: 10px;
    img{
      width: 100%;
    }
    a{
      font: 18px/1.5 Oswald;
    }
  }
  .menu-item:last-child{
    margin-bottom: 0;
  }
}